<template>
	<view class="page">
		<view class="cu-list menu-avatar margin-top">
			<view class="cu-item" v-for="(item, index) in getList" :key="index" >
				<block v-if="item.user.portrait">
					<view class="cu-avatar round lg" :style="'background-image:url(' + item.user.portrait +');'"></view>
				</block>
				<block v-else>
					<view class="cu-avatar round lg">
						<text class="cuIcon-people"></text>
					</view>
				</block>
				<view class="content">
					<view class="text-sm flex">
						<view class="text-cut">
							<block v-if="item.name">{{item.name}}({{item.user.cellphone}})</block>
							<block v-else>{{item.user.cellphone}}</block>
						</view>
					</view>
					<view class="text-grey">店长</view>
				</view>
				<button v-if="item.user_id !== item.applyUserId" class="cu-btn round bg-red" @click="deleteStaffing(item)">删除</button>
			</view>
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="submit-content go-indent">
				<text class="submit-red" @click="goDetails()">添加员工</text>
			</view>
		</view>
	</view>
</template> 

<script>
	import Staff from '../../api/staffing'
	export default {
		data() {
			return {
				page:1,
				getList: []
				}
		},
		
		onLoad: function(options) {
			this.loadData()
		},
		methods: {
			//获取订单列表
			async loadData(source){
				const that = this
				await Staff.getList({
					limit: 100,
					page: this.page
				},function(res){
					that.getList = res.data
				})
			},
			//刷新
			refreshStaffList(index, data){
				this.loadData()
			},
			//编辑
			goDetails(){
				uni.navigateTo({
					url: `/pages/store/add-staff`
				})
			},
			//删除
			deleteStaffing(res){
				const that = this
				Staff.deleteSubmit(res.id,function(res){
					that.$api.msg('删除成功')
					that.loadData()
				})
			}
		},
	}
</script>

<style lang="scss">
	page, .page{
		background: $page-color-base;
		height: 100%;
	}
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-left: 30upx;
		}
		.price-tip{
			color: $base-color;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: $base-color;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
		}
		.submit-content{
			display:flex;
			flex-direction:row-reverse;
			align-items:center;
			justify-content: center;
			height: 100%;
			.submit-red{
				display:flex;
				flex-direction:row-reverse;
				align-items:center;
				justify-content: center;
				width: 100%;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: $base-color;
			}
			.submit-default{
				display:flex;
				align-items:center;
				justify-content: center;
				width: 200upx;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: #909399;
			}
		}
		.go-indent{
			width: 100%;
		}
	}
</style>
